<template>
  <div style="padding-top: 40px; background: #f1f1f3">
    <div class="row justify-content-center" style="margin-bottom: 30px">
      <div class="col-md-10">
        <div class="col-md-12">
          <div class="card">
            <div class="card-body">
              <div style="display: flex">
                <div>
                  <h3 style="font-family: fantasy; margin-bottom: 0px">Money management detail</h3>
                </div>
                <div style="margin: auto 0 auto auto;">
                  <router-link to="/">Home</router-link> / 
                  <span>Detail</span>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="col-md-12" style="display: flex; padding-left: 0px; padding-right: 0px;">
          <div class="col-md-3">
            <div class="card" >
              <div class="card-body">
                <div class="media">
                  <div class="media-left">
                    <span class="icoleaf bg-primary text-white">
                      <i class="fa fa-eur"></i>
                    </span>
                  </div>
                  <div class="media-body">
                    <h3 class="info-count text-blue">{{this.Moneys.startingBank}}</h3>
                    <p class="info-text font-12" style="margin-bottom: 0px;">Starting Bank</p>
                    <span class="hr-line"></span>
                    <p class="info-ot font-15" >
                      Today in profits
                      <span class="label label-rounded label-success">0.00</span>
                    </p>
                  </div>
                </div>
              </div>
            </div>   
          </div>
          <div class="col-md-3">
            <div class="card">
              <div class="card-body">
                <div class="media">
                  <div class="media-left">
                    <span class="icoleaf bg-primary text-white">
                      <i class="fa fa-cart-plus"></i>
                    </span>
                  </div>
                  <div class="media-body">
                    <h3 class="info-count text-blue">{{this.Moneys.targetProfit}}</h3>
                    <p class="info-text font-12" style="margin-bottom: 0px;">Target Profit</p>
                    <span class="hr-line"></span>
                    <p class="info-ot font-15" >
                      Today in profits
                      <span class="label label-rounded label-success">0.00%</span>
                    </p>
                  </div>
                </div>
              </div>
            </div> 
          </div>
          <div class="col-md-3">
            <div class="card">
              <div class="card-body">
                <div class="media">
                  <div class="media-left">
                    <span class="icoleaf bg-primary text-white">
                      <i class="fa fa-cart-arrow-down "></i>
                    </span>
                  </div>
                  <div class="media-body">
                    <h3 class="info-count text-blue">{{this.Moneys.stopLoss}}</h3>
                    <p class="info-text font-12" style="margin-bottom: 0px;">Daily stop losses</p>
                    <span class="hr-line"></span>
                    <p class="info-ot font-15" >
                      Today losses
                      <span class="label label-rounded label-success">0.00%</span>
                    </p>
                  </div>
                </div>
              </div>
            </div> 
          </div>
          <div class="col-md-3">
            <div class="card">
              <div class="card-body">
                <div class="media">
                  <div class="media-left">
                    <span class="icoleaf bg-primary text-white">
                      <i class="fa fa-money"></i>
                    </span>
                  </div>
                  <div class="media-body">
                    <h3 class="info-count text-blue">€{{recentCurrentBank}}</h3>
                    <p class="info-text font-12" style="margin-bottom: 0px;">Current Bank</p>
                    <span class="hr-line"></span>
                    <p class="info-ot font-15" >
                      Profit / Losses
                      <span class="label label-rounded label-danger">€ {{recentProfitLoss}}</span>
                      <span class="label label-rounded label-danger">{{recentPercentProfitLoss}} %</span>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div> 
        </div>
      </div>
      <div class="table_1">
        <div class="card">
          <div class="card-body">
            <div class="label_1">
              <label></label>
            </div>
            <div class="col-md-12">
              <table class="table table-striped" style="text-align: center; font-family: 'FontAwesome';">
                <thead class="thead-dark">
                  <tr>
                    <th></th>
                    <th>Trades in profits</th>
                    <th>Best profit</th>
                    <th>Trades losses</th>
                    <th>Worse loss</th>
                    <th>Today in profits</th>
                    <th>Today losses</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td class="table_row_1" style="width: 25px;">n&#176;</td>
                    <td style="background-color: #7df795;">{{profit_number_N}}</td>
                    <td style="background-color: #7df795;">{{best_profit_N}}</td>
                    <td style="background-color: #ffbdbd;">{{loss_number_N}}</td>
                    <td style="background-color: #ffbdbd;">{{worse_loss_N}}</td>         
                    <td>{{today_profit_N}}</td>
                    <td>{{today_loss_N}}</td>
                  </tr>
                  <tr>
                    <td class="table_row_1">Eur</td>
                    <td style="background-color: #7df795;">{{total_profit_Eur}}</td>
                    <td style="background-color: #7df795;">{{max_profit_Eur}}</td>
                    <td style="background-color: #ffbdbd;">{{total_loss_Eur}}</td>
                    <td style="background-color: #ffbdbd;">{{max_loss_Eur}}</td>         
                    <td>{{total_today_profit}}</td>
                    <td>{{total_today_loss}}</td>
                  </tr>
                  <tr>
                    <td class="table_row_1">%</td>
                    <td style="background-color: #7df795;">{{percent_profit}}%</td>
                    <td style="background-color: #7df795;">{{percent_best_profit}}%</td>
                    <td style="background-color: #ffbdbd;">{{percent_loss}}%</td>
                    <td style="background-color: #ffbdbd;">{{percent_worse_loss}}%</td>         
                    <td>{{percent_today_profit}}%</td>
                    <td>{{percent_today_loss}}%</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="label_1">
            <label></label>
            </div>
            <div class="col-md-12">
              <table class="table table-striped" style="text-align: center; font-family: 'FontAwesome';">
                <thead class="thead-dark">
                  <tr>
                    <th>Index</th>
                    <th>Events</th>
                    <th style="width: 100px;">Date</th>
                    <th style="width: 120px;">Current Bank(€)</th>
                    <th>Stake (points)</th>
                    <th style="max-width: 60px;">Back/Lay</th>
                    <th style="width: 70px;">Odd</th>
                    <th style="max-width: 85px;">Stake (€:Eur)</th>
                    <th style="max-width: 110px;">Profit (€:potential)</th>
                    <th style="max-width: 85px;">Risk(€)</th>
                    <th style="width: 80px;">Real Profit(€)</th>
                    <th style="max-width: 100px;">Balance(€)</th>
                    <th style="width: 90px;">Edit</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(detail, index) in Details" :key="detail._id">
                    <td>{{index + 1}}</td>
                    <td class="table2_td" style="padding: 0em;">
                      <input type="text" class="form-control table2_form" style="font-size: 16px; height: calc(2.25rem + 14px); border: 0px; border-radius: 0px; text-align: center;" :id="detail._id" v-model="detail.events" v-on:change="changeEvent" required>
                    </td>
                    <td class="table2_td" style="padding: 0em;">
                      <input type="date" class="form-control table2_form" style="font-size: 16px; height: calc(2.25rem + 14px); border: 0px; border-radius: 0px; text-align: center;" :id="detail._id" v-model="detail.date" v-on:change="changeDate" required>
                    </td>
                    <td class="table2_td">{{detail.currentBank}}</td>
                    <td class="table2_td" style="padding: 0em; width: 70px;">
                      <select v-model="detail.stake_points" @change.prevent="changeStakePoints(detail._id, detail.backLay, detail.currentBank, detail.odd, detail.stake_points)" style="width: 100%; height: 50px; border-color: white;">
                        <option>0.25</option>
                        <option>0.5</option>
                        <option>0.75</option>
                        <option>1.00</option>
                        <option>1.25</option>
                        <option>1.5</option>
                        <option>1.75</option>
                        <option>2.00</option>
                      </select>
                    </td>
                    <td class="table2_td" style="padding: 0em;">
                      <select v-model="detail.backLay" @change.prevent="changeBackLay(detail._id, detail.backLay, detail.currentBank, detail.odd, detail.stake_points)" style="width: 100%; height: 50px; border-color: white;">
                        <option>Back</option>
                        <option>Lay</option>
                      </select>
                    </td>
                    <td class="table2_td" style="padding: 0em;">
                      <input type="text" class="form-control table2_form" style="font-size: 16px; height: calc(2.25rem + 14px); border: 0px; border-radius: 0px; text-align: center;" v-model="detail.odd" @change.prevent="changeOdd(detail._id, detail.backLay, detail.currentBank, detail.odd, detail.stake_points)" required>
                    </td>
                    <td class="table2_td" style="background-color: #ffbdbd;">
                      {{detail.stake_eur}}
                    </td>
                    <td class="table2_td">{{detail.profit}}</td>
                    <td class="table2_td">{{detail.risk}}</td>
                    <td class="table2_td" style="padding: 0em;">
                      <input v-if="detail.stake_points && detail.backLay && detail.odd" type="text" class="form-control table2_form" style="font-size: 16px; height: calc(2.25rem + 14px); border: 0px; border-radius: 0px; text-align: center;" v-model="detail.realProfit" @change.prevent="changeRealProfit(index, detail._id, detail.realProfit)" required>
                    </td>
                    <td class="table2_td">{{detail.balance}}</td>
                    <td style="padding: 5px; display: flex">
                      <!-- <button type="button" class="btn1 btn-outline-info" style="border-color: #17a2b8; margin-right: 5px;"><i class="fa fa-calculator" @click.prevent="calculate(index, detail._id, detail.backLay, detail.currentBank, detail.odd, detail.stake_points, detail.realProfit)"></i></button>   -->
                      <button type="button" class="btn1 btn-outline-info" style="margin-right: 5px; border-color: #17a2b8;" @click.prevent="createNewRecord(index, detail.currentBank, detail.realProfit)"><i class="fa fa-plus-circle"></i></button>
                      <button type="button" class="btn1 btn-outline-info" style="border-color: #17a2b8;"><i class="fa fa-trash-o" @click.prevent="deleteTable2List(detail._id)"></i></button>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from "axios";
  import swal from "sweetalert2";

  export default {
    data() {
      return {
        money: {
          name: '',
          startingBank: '',
          targetProfit: '',
          stopLoss: ''
        },
        Moneys: [],
        Details: [],
        bank:[],
        realProfit: [],
        stakeEur: [],
        balance: [],
        balance_before: [],
        profitPotential: [],
        risk: [],
        recentCurrentBank: [],
        recentProfitLoss: [],
        recentPercentProfitLoss: [],

        profit_number_N: [],
        best_profit_N: [],
        total_profit_Eur: [],
        max_profit_Eur: [],
        percent_profit: [],
        percent_best_profit: [],

        loss_number_N: [],
        worse_loss_N: [],
        total_loss_Eur: [],
        max_loss_Eur: [],
        percent_loss: [],
        percent_worse_loss: [],

        today_profit_N: [],
        today_loss_N: [],
        total_today_profit: [],
        total_today_loss: [],
        percent_today_profit: [],
        percent_today_loss: [],

        fMonth: [],
        stopLoss_state: [],
      }
    },
    created() {
      let apiURL = `http://localhost:8081/api/moneys/list/${this.$route.params.id}` ;
      axios.post(apiURL).then(res => {
          this.Moneys = res.data[0];
      }).catch(error => {
          console.log(error)
      });
      let detailURL = `http://localhost:8081/api/details/list/${this.$route.params.id}` ;
      axios.post(detailURL).then(res => {
        this.Details = res.data;
        // All Table Data
        const data = this.Details.map((res) => res.realProfit)
        const resultMinus = [];
        const resultPlus = [];
        data.forEach((e) => {
          const minus = e.slice(0, 1);
          e = +e;
          if(minus == "-") {
            resultMinus.push(e)
          } else {
            resultPlus.push(e)
          }
        });
        // Plus Data
        var sum_profit = resultPlus.reduce(function(a, b){
          return a + b;
        }, 0);
        this.total_profit_Eur = sum_profit
        this.max_profit_Eur = Math.max(...resultPlus);
        // Minus Data
        var sum_loss = resultMinus.reduce(function(a, b){
          return a + b;
        }, 0);
        this.total_loss_Eur = sum_loss
        this.max_loss_Eur = Math.min(...resultMinus);

        this.profit_number_N = resultPlus.length
        this.loss_number_N = resultMinus.length

        if(this.profit_number_N > 0) {
          this.best_profit_N = "1";
        } else if (this.profit_number_N < 1) {
          this.best_profit_N = "0";
        } else {
          return;
        }
        if(this.loss_number_N > 0) {
          this.worse_loss_N = "1";
        } else if (this.loss_number_N < 1) {
          this.worse_loss_N = "0";
        } else {
          return;
        }
        // Today Table Data
        const fYear = new Date().getFullYear();
        const fDay = new Date().getDate();
        const month_value = new Date().getMonth() + 1
        if(month_value < 10) {
          this.fMonth = "0" + month_value;
        } else if (month_value > 9){
          this.fMonth = month_value;
        } else {
          return;
        }
        const today = fYear + "-" + this.fMonth + "-" + fDay;
        const resultTodayList = [];
        this.Details.forEach((e) => {
          if(e.date == today)
            resultTodayList.push(e)
        })
        const todayData = resultTodayList.map((res) => res.realProfit)
        const resultTodayMinus = [];
        const resultTodayPlus = [];
        todayData.forEach((e) => {
          const minus = e.slice(0, 1);
          e = +e;
          if(minus == "-") {
            resultTodayMinus.push(e)
          } else {
            resultTodayPlus.push(e)
          }
        });
        this.today_profit_N = resultTodayPlus.length
        this.today_loss_N = resultTodayMinus.length
        // Today Profit Data
        var sum_today_profit = resultTodayPlus.reduce(function(a, b){
          return a + b;
        }, 0);
        this.total_today_profit = sum_today_profit
        // Today Loss Data
        var sum_today_loss = resultTodayMinus.reduce(function(a, b){
          return a + b;
        }, 0);
        this.total_today_loss = sum_today_loss
        // Percentage Data
        const starting_bank = this.Moneys.startingBank.slice(1)

        const percent_profit = this.total_profit_Eur/starting_bank*100; 
        this.percent_profit = percent_profit.toString().slice(0, 5)

        const percent_best_profit = this.max_profit_Eur/starting_bank*100;
        this.percent_best_profit = percent_best_profit.toString().slice(0, 5)

        const percent_loss = this.total_loss_Eur/starting_bank*100;
        this.percent_loss = percent_loss.toString().slice(0, 5);

        const percent_worse_loss = this.max_loss_Eur/starting_bank*100;
        this.percent_worse_loss = percent_worse_loss.toString().slice(0, 6)

        const percent_today_profit = this.total_today_profit/starting_bank*100;
        this.percent_today_profit = percent_today_profit.toString().slice(0, 5);

        const percent_today_loss = this.total_today_loss/starting_bank*100;
        this.percent_today_loss = percent_today_loss.toString().slice(0, 6)

        const recentCurrentBank = this.Details[this.Details.length-1].currentBank.toString().slice(1);
        const recentRealProfit = this.Details[this.Details.length-1].realProfit;
        this.recentCurrentBank = Number(recentCurrentBank) + Number(recentRealProfit)

        const recentProfitLoss = Number(this.total_profit_Eur) + Number(this.max_profit_Eur) + Number(this.total_loss_Eur) + Number(this.max_loss_Eur)
        this.recentProfitLoss = recentProfitLoss.toString().slice(0, 5)

        const recentPercentProfitLoss = recentProfitLoss/starting_bank*100
        this.recentPercentProfitLoss = recentPercentProfitLoss.toString().slice(0, 5)
      }).catch(error => {
          console.log(error)
      });
    },
    methods: {
      // Update Event
      changeEvent(e) {
        const events = e.target.value
        const id = e.target.id
        let updateDetailURL = 'http://localhost:8081/api/details/updateEvent';
        const data = {id, events}
        axios.put(updateDetailURL, data).then(() => {
          console.log('Success')
        }).catch(error => {
          console.log(error)
        });
      },
      // Update Date
      changeDate(e) {
        const date = e.target.value
        const id = e.target.id
        let updateDetailURL = 'http://localhost:8081/api/details/updateDate';
        const data = {id, date}
        axios.put(updateDetailURL, data).then(() => {
          console.log('Success')
        }).catch(error => {
          console.log(error)
        });
      },
      // Update Back/Lay
      changeBackLay(id1, backLay_value, currentBank, odd, stake_points) {
        const backLay = backLay_value
        const id = id1
        let updateDetailURL = 'http://localhost:8081/api/details/updateBackLay';
        const data = {id, backLay}
        axios.put(updateDetailURL, data).then(() => {
          console.log('Success')
        }).catch(error => {
          console.log(error)
        });
        this.calculate1(id, backLay, currentBank, odd, stake_points)
      },
      // Update Stake(points)
      changeStakePoints(id1, backLay, currentBank, odd, stake_points) {
        const stake = stake_points
        const id = id1
        let updateDetailURL = 'http://localhost:8081/api/details/updateStakePoints';
        const data = {id, stake}
        axios.put(updateDetailURL, data).then(() => {
          console.log('Success')
        }).catch(error => {
          console.log(error)
        });
        this.calculate1(id, backLay, currentBank, odd, stake_points)
      },
      // Update Odd
      changeOdd(id1, backLay, currentBank, odd1, stake_points) {
        const odd = odd1
        const id = id1
        let updateDetailURL = 'http://localhost:8081/api/details/updateOdd';
        const data = {id, odd}
        axios.put(updateDetailURL, data).then(() => {
          console.log('Success')
        }).catch(error => {
          console.log(error)
        });
        this.calculate1(id, backLay, currentBank, odd, stake_points)
      },
      // Update Real Profit
      changeRealProfit(index, rowId, real_profit) {
        const realProfit = real_profit
        const id = rowId
        let updateDetailURL = 'http://localhost:8081/api/details/updateRealProfit';
        const data = {id, realProfit}
        axios.put(updateDetailURL, data).then(() => {
          console.log('Success')
        }).catch(error => {
          console.log(error)
        });
        this.calculate2(index, id, realProfit)
      },
      calculate1(id, backLay, currentBank, odd, stake_points) {
        // Calculate Stake(Eur) Value
        if (backLay == "Lay") {
          const currentBank_value = currentBank.slice(1)
          const stakeEur = ((odd/(odd-1))*stake_points*currentBank_value/100) - stake_points*currentBank_value/100;
          this.stakeEur = stakeEur.toString()
          this.profitPotential = this.stakeEur;
          const risk = stake_points*currentBank_value/100;
          this.risk = risk.toString();

        } else if (backLay == "Back") {
          const currentBank_value = currentBank.slice(1)
          const stakeEur = currentBank_value*stake_points/100;
          this.stakeEur = stakeEur.toString()
          const profit_potential = (stakeEur*odd)-stakeEur;
          this.profitPotential = profit_potential.toString();
          const risk = stakeEur;
          this.risk = risk.toString();
        } else {
          return;
        }
        const stake_eur_value = this.stakeEur.slice(0, 5);
        const profit_potential_value = this.profitPotential.slice(0, 5);
        const risk_value = this.risk.slice(0, 5);
        const data1 = {id, stake_eur_value, profit_potential_value, risk_value}
        let updateDetailURL1 = 'http://localhost:8081/api/details/addStakeEur';
        axios.put(updateDetailURL1, data1).then(() => {
          let detailURL = `http://localhost:8081/api/details/list/${this.$route.params.id}`;
          axios.post(detailURL).then(res => {
              this.Details = res.data;
          }).catch(error => {
              console.log(error)
          });
        }).catch(error => {
          console.log(error)
        });
      },
      calculate2(index, id, real_profit) {
        // Calculate Balance
        if(index == 0) {
          const balance = Number(real_profit);
          let updateDetailURL = 'http://localhost:8081/api/details/addStakeEur';
          const stake_eur_value = this.stakeEur.slice(0, 5);
          const profit_potential_value = this.profitPotential.slice(0, 5);
          const risk_value = this.risk.slice(0, 5);
          const data = {id, stake_eur_value, profit_potential_value, risk_value, balance}
          axios.put(updateDetailURL, data).then(() => {
            let detailURL = `http://localhost:8081/api/details/list/${this.$route.params.id}`;
            axios.post(detailURL).then(res => {
                this.Details = res.data;
            }).catch(error => {
                console.log(error)
            });
          }).catch(error => {
            console.log(error)
          });
        } else {
          let detailURL = `http://localhost:8081/api/details/customlist/${this.$route.params.id}/${index}`;
          axios.post(detailURL).then(res => {
            const balance_before = res.data[0].balance
            const balance_value = Number(real_profit) + Number(balance_before)

            let updateDetailURL = 'http://localhost:8081/api/details/addStakeEur';
            const stake_eur_value = this.stakeEur.slice(0, 5);
            const profit_potential_value = this.profitPotential.slice(0, 5);
            const risk_value = this.risk.slice(0, 5);
            const balance = balance_value.toString().slice(0, 5);
            const data = {id, stake_eur_value, profit_potential_value, risk_value, balance}
            axios.put(updateDetailURL, data).then(() => {
              let detailURL = `http://localhost:8081/api/details/list/${this.$route.params.id}`;
              axios.post(detailURL).then(res => {
                  this.Details = res.data;
              }).catch(error => {
                  console.log(error)
              });
            }).catch(error => {
              console.log(error)
            });
          })
        }
      },
      createNewRecord(index, currentBank, realProfit) {
        const sign = realProfit.slice(0, 1)
        const currentBank_value = currentBank.slice(1)
        if(sign == "-") {
          const realProfit_value = realProfit.slice(1)
          this.bank = currentBank_value - realProfit_value
        } else {
          this.bank = Number(currentBank_value) + Number(realProfit);
        }
        const percent_today_loss_value = this.percent_today_loss.toString().slice(1)
        const stopLoss = Number(percent_today_loss_value)-Number(this.percent_today_profit)
        const stopLoss_value = stopLoss.toString().slice(1, 5);
        const percent = this.Moneys.stopLoss.indexOf("%");
        const percent_stopLoss = this.Moneys.stopLoss.toString().slice(0, percent)
        this.stopLoss_state == "enable";
        const bank = this.bank;
        const id = this.$route.params.id
        const index1 = index + 1;
        const index2 = index + 2;
        const data = { id, bank, index1}

        let detailURL = `http://localhost:8081/api/details/customlist/${this.$route.params.id}/${index2}`;
        axios.post(detailURL).then(res => {
          const result = res.data[0]
          if (result) {
            swal.fire("Alert!", "You can't add new record", "error");
          } else if (stopLoss_value > percent_stopLoss) {
            swal.fire("Stop Loss!", "You can't add new record", "error");
          } else {
            let newRecordURL = "http://localhost:8081/api/details/createNewRecord";
            axios
            .post(newRecordURL, data)
            .then(() => {
              let detailURL = `http://localhost:8081/api/details/list/${this.$route.params.id}`;
              axios.post(detailURL).then(res => {
                  this.Details = res.data;
              }).catch(error => {
                  console.log(error)
              });
            })
            .catch((error) => {
              console.log(error);
            });
          }
        })
      },
      deleteTable2List(id) {
      let apiURL = `http://localhost:8081/api/details/delete/${id}`;
      let indexOfArrayItem = this.Details.findIndex((i) => i._id === id);
      swal
        .fire({
          title: "Are you sure?",
          text: "You won't be able to revert this!",
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Yes, delete it!",
        })
        .then((result) => {
          if (result.value) {
            axios
              .delete(apiURL)
              .then(() => {
                this.Details.splice(indexOfArrayItem, 1);
                swal("Deleted!", "This record has been deleted.", "success");
              })
              .catch((error) => {
                console.log(error);
              });
          }
        });
    },
    }
  }
</script>

